<template>
  <div>
    <v-navigation-drawer class="white" v-model="drawer" fixed clipped disable-resize-watcher app>
      <v-toolbar flat class="transparent">
        <v-list class="pa-0">
          <v-list-tile avatar>
            <v-list-tile-avatar>
              <img
                src="data:image/jpeg;base64,"
              />
            </v-list-tile-avatar>

            <v-list-tile-content>
              <v-list-tile-title>Napoleon Hill</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-toolbar>
      <v-divider></v-divider>
      <v-list dense>
        <div v-for="(item, i) in items">
          <v-layout v-if="item.heading" :key="i" row align-center>
            <v-flex xs6>
              <v-subheader>{{ item.heading }}</v-subheader>
            </v-flex>
          </v-layout>

          <v-divider v-else-if="item.divider" :key="i" dark class="my-3"></v-divider>
          <v-list-tile v-else :key="i" :to="item.to">
            <v-list-tile-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-tile-action>

            <v-list-tile-content>
              <v-list-tile-title>{{item.text}}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </div>
      </v-list>
    </v-navigation-drawer>

    <v-toolbar color="amber" app fixed clipped-left>
      <v-toolbar-side-icon @click="drawer = !drawer"></v-toolbar-side-icon>
      <span class="title m1-3 mr-5">
        Master
        <span class="font-weight-light">Mind</span>
      </span>
      <v-text-field
        name="search"
        label="Search"
        id="searchbar"
        flat
        hide-details
        solo-inverted
        prepend-inner-icon="search"
      ></v-text-field>
      <v-spacer></v-spacer>
    </v-toolbar>

    <v-content class="white" fluid grid-list-md text-xs-center>
      <router-view></router-view>
    </v-content>
  </div>
</template>

<script>
export default {
  data() {
    return {
      drawer: null,
      items: [
        { icon: "home", text: "Inicio", to: "/" },
        { icon: "lightbulb_outline", text: "Ideas", to: "/ideas" },
        {
          icon: "alarm",
          text: "Administracin del tiempo",
          to: "/administracion-del-tiempo"
        },
        {
          icon: "touch_app",
          text: "Objetivos diarios",
          to: "/objetivos-diarios"
        },
        { divider: true },
        { heading: "Labels" },
        { icon: "add", text: "Create new label" },
        { divider: true },
        { icon: "archive", text: "Archive" },
        { icon: "delete", text: "Trash" },
        { divider: true },
        { icon: "settings", text: "Settings" },
        { icon: "chat_bubble", text: "Trash" },
        { icon: "help", text: "Help" },
        { icon: "phonelink", text: "App downloads" },
        { icon: "keyboard", text: "Keyboard shortcuts" }
      ]
    };
  },
  methods: {
    send: function(tit, txt) {
      if (tit.length > 0 || txt.length) {
        this.notes.unshift({ title: tit, text: txt });
        this.title = "";
        this.text = "";
      }
      this.seen = false;
    },
    closeMenu: function() {
      if (this.isMenu) {
        this.inMenu = true;
        this.isMenu = false;
      } else {
        this.inMenu = false;
      }
    },
    opMenu: function(op, index) {
      if (op == 0) {
        this.notes.splice(index, 1);
      } else if (op == 1) {
        alert("Change Labels");
      }
      this.inMenu = false;
    }
  }
};
</script>

<style lang="scss" scoped>
#keep main .container {
  height: 660px;
}

.navigation-drawer__border {
  display: none;
}

.text {
  font-weight: 400;
}
</style>